
body {
  margin: 0;
  font-family: Roboto;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  margin: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  min-height: 100vh;

  background: #141414;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  display: flex;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

h1 {
  font-family: Roboto Mono;
  font-weight: 500;
  font-size: 24px;
  line-height: 34px;
}

h2 {
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 1.75px;
  text-transform: uppercase;
  color: #0A6CFF;
}

h3 {
  font-family: Roboto Mono;
  font-size: 14px;
  line-height: 24px;
  color: #DCDCDC;
}

div.promo {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}
div.column {
  margin: 35px;
  max-width: 220px;
} 

div.column div.caption {
  margin-top: 10px;
  text-align: center;
}

div.button {
  padding: 8px 24px;
  color: #fff;
  background: #0A6CFF;
  border-radius: 4px;
}

a {
  text-decoration: underline;
  font-weight: 500;
  color: unset;
  cursor: pointer;
}

input:-webkit-autofill {
  -webkit-box-shadow:0 0 0 50px #141414 inset; 
  box-shadow:0 0 0 50px #141414 inset;
  -webkit-text-fill-color: #ffffff; 
}

input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0 50px #141414 inset;
  box-shadow: 0 0 0 50px #141414 inset;
  -webkit-text-fill-color: #ffffff;
} 
#root .MuiOutlinedInput-notchedOutline{
  border: 1px solid #383838;
}
#root .MuiInputBase-root, #root .MuiInputBase-input{
  color: #ffffff;
}
#root .MuiInputLabel-root{
  color: #676767;
}
